<template>
  <div class="interface-history-tx">
    <div class="spinner-container">
      <i v-show="!txLoaded" class="fa fa-spin fa-spinner fa-lg" />
    </div>
    <p v-if="txLoaded && transactions.length === 0" class="text-center">
      Transaction history is empty
    </p>
    <ul v-else-if="txLoaded" class="tx-history">
      <li v-for="tx in transactions" :key="tx.hash">
        <div class="tx-time">
          <p>{{ isReceived(tx) ? 'Received' : 'Sent' }}</p>
          <p>{{ getDate(tx) }}</p>
        </div>
        <div>
          <span class="tx-value">{{ getValue(tx) }}</span>
          <span class="tx-curr">{{ ' ' + getCurrSymbol(tx) }}</span>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  props: {
    transactions: {
      type: Array,
      default: function() {
        return [];
      }
    },
    txLoaded: {
      type: Boolean,
      default: function() {
        return false;
      }
    }
  },
  computed: {
    ...mapGetters({
      account: 'account'
    })
  },
  methods: {
    isReceived(tx) {
      return tx.to.toUpperCase() === this.account.address.toUpperCase();
    },
    getDate(tx) {
      return new Date(tx.timestamp * 1000).toLocaleString();
    },
    getValue(tx) {
      return tx.value;
    },
    getCurrSymbol(tx) {
      return tx.symbol;
    }
  }
};
</script>

<style lang="scss" scoped>
@import 'InterfaceHistoryTx.scss';
</style>
