<template>
  <div class="eth-wallet-container">
    <balance-block
      :curr-symbol="'ETH'"
      :balance="balance"
      :get-balance="getBalance"
      class="balance-block"
    />
    <interface-history-tx :transactions="transactions" :tx-loaded="txLoaded" />
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import ethplorer from '@/services/ethplorer.js';
import InterfaceHistoryTx from '../../components/InterfaceHistoryTx';
import { Toast } from '@/helpers';
import BalanceBlock from '../../components/BalanceBlock';
import BigNumber from 'bignumber.js';

export default {
  components: {
    'interface-history-tx': InterfaceHistoryTx,
    'balance-block': BalanceBlock
  },
  props: {},
  data() {
    return {
      transactions: [],
      txLoaded: false
    };
  },
  computed: {
    ...mapGetters({
      account: 'account',
      web3: 'web3'
    }),
    balance() {
      return this.web3.utils.fromWei(
        new BigNumber(this.account.balance).toFixed(),
        'ether'
      );
    }
  },
  mounted() {
    this.getTxs();
    this.getBalance();
  },
  methods: {
    getBalance() {
      if (this.account.address) {
        this.web3.eth
          .getBalance(this.account.address.toLowerCase())
          .then(res => {
            this.$store.dispatch('setAccountBalance', res);
          })
          .catch(err => {
            Toast.responseHandler(err, Toast.ERROR);
          });
      }
    },
    getTxs() {
      ethplorer
        .getEthTx(this.account.address)
        .then(res => {
          this.transactions = res;
          this.txLoaded = true;
        })
        .catch(err => {
          this.txLoaded = true;
          Toast.responseHandler(err, Toast.ERROR);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
@import 'EthWalletContainer.scss';
</style>
